img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style-type: none;
  padding: 0;
  -webkit-padding-start: 0;
  padding-inline-start: 0;
}

input::-ms-clear {
  display: none;
}

a,
button {
  display: block;
  box-sizing: border-box;
  padding: 0;

  color: inherit;
  font: inherit;
  text-decoration: none;
  text-align: left;

  background-color: inherit;
  border: none;

  cursor: pointer;
}


